@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  src: local("Montserrat Regular"),
       local("Montserrat-Regular"),
       url("../fonts/montserrat-regular.woff2") format("woff2"),
       url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}

